import React from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Wallet, Check, Phone } from 'lucide-react';
import { cn } from '@/lib/utils';

interface WalletData {
  id: string;
  name: string;
  phone: string;
  isDefault: boolean;
}

interface WalletsListDialogProps {
  isOpen: boolean;
  onClose: () => void;
  wallets: WalletData[];
  walletBalances: Record<string, number>;
  onSelectWallet: (walletId: string) => void;
  selectedWalletId?: string;
}

export function WalletsListDialog({
  isOpen,
  onClose,
  wallets,
  walletBalances,
  onSelectWallet,
  selectedWalletId
}: WalletsListDialogProps) {
  
  const handleSelect = (walletId: string) => {
    onSelectWallet(walletId);
    onClose();
  };

  const totalBalance = Object.values(walletBalances).reduce((sum, val) => sum + (Number(val) || 0), 0);

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-md bg-gradient-to-br from-white to-gray-50 border-0 shadow-2xl">
        <DialogHeader className="space-y-4 pb-4 border-b border-gray-100">
          <div className="flex items-center gap-3">
            <div className="p-3 bg-blue-100 rounded-full">
              <Wallet className="w-6 h-6 text-blue-600" />
            </div>
            <div className="space-y-1">
              <DialogTitle className="text-xl font-bold text-gray-900">
                المحافظ المتاحة
              </DialogTitle>
              <p className="text-sm text-gray-500 font-medium">
                إجمالي الرصيد: <span className="text-blue-600 font-bold text-base">{totalBalance.toLocaleString('en-US')} جنيه</span>
              </p>
            </div>
          </div>
        </DialogHeader>
        
        <ScrollArea className="max-h-[60vh] pr-4 -mr-4">
          <div className="grid gap-3 py-2">
            {wallets.length === 0 ? (
              <div className="text-center py-8 text-gray-500 bg-gray-50 rounded-xl border-2 border-dashed border-gray-200">
                <p>لا توجد محافظ مضافة</p>
              </div>
            ) : (
              wallets.map((wallet) => {
                const balance = walletBalances[wallet.id] || 0;
                const isSelected = selectedWalletId === wallet.id;
                
                return (
                  <button
                    key={wallet.id}
                    onClick={() => handleSelect(wallet.id)}
                    className={cn(
                      "w-full group relative flex items-center justify-between p-4 rounded-xl border-2 transition-all duration-200",
                      isSelected 
                        ? "border-blue-500 bg-blue-50/50 shadow-md ring-1 ring-blue-200" 
                        : "border-transparent bg-white hover:bg-gray-50 hover:border-gray-200 shadow-sm hover:shadow-md"
                    )}
                  >
                    <div className="flex items-center gap-4">
                      <div className={cn(
                        "w-12 h-12 rounded-full flex items-center justify-center transition-colors",
                        isSelected ? "bg-blue-100 text-blue-600" : "bg-gray-100 text-gray-500 group-hover:bg-gray-200"
                      )}>
                        <Wallet className="w-6 h-6" />
                      </div>
                      
                      <div className="flex flex-col items-start gap-1">
                        <span className={cn(
                          "font-bold text-lg",
                          isSelected ? "text-blue-900" : "text-gray-900"
                        )}>
                          {wallet.name}
                        </span>
                        <div className="flex items-center gap-1.5 text-gray-500">
                          <Phone className="w-3.5 h-3.5" />
                          <span className="text-sm font-medium dir-ltr">{wallet.phone}</span>
                        </div>
                      </div>
                    </div>

                    <div className="flex flex-col items-end gap-1">
                      <span className={cn(
                        "font-extrabold text-lg",
                        balance > 0 ? "text-emerald-600" : "text-gray-400"
                      )}>
                        {balance.toLocaleString('en-US')}
                        <span className="text-xs mr-1 font-medium text-gray-400">جنيه</span>
                      </span>
                      {isSelected && (
                        <div className="flex items-center gap-1 text-xs font-bold text-blue-600 bg-blue-100 px-2 py-0.5 rounded-full">
                          <Check className="w-3 h-3" />
                          مختار
                        </div>
                      )}
                    </div>
                  </button>
                );
              })
            )}
          </div>
        </ScrollArea>
      </DialogContent>
    </Dialog>
  );
}
